.header {
  display: flex;
  justify-content: center;
  background-color: goldenrod;
  border-bottom: 1px solid gray;
}
.title {
  font-size: 1rem;
}

@media (min-width: 641px) {
  .title {
    font-size: 1.8rem;
  }
}

@media (min-width: 1008px) {
  .title {
    font-size: 2.5rem;
  }
}
